
import { defineComponent } from "vue";
import ErrorMessage from "@/components/ErrorMessage.vue";
import ValidateError from "@/components/ValidateError.vue";
import {
  showTherapistApi,
  updateTherapistApi,
  destroyTherapistApi,
} from "@/api/therapists";

export default defineComponent({
  name: "TherapistUpdate",
  components: {
    ErrorMessage,
    ValidateError,
  },
  data: () => ({
    isShow: false,
    message: "",
    submitDisable: false,
    validateErrors: {} as any,
    items: {} as any,
    therapist: {} as any,
  }),
  mounted: async function () {
    const result = await showTherapistApi(this.$route.params.id);
    if (!result.success) {
      this.commonError(result);
      return;
    }
    this.therapist = result.data;
    this.init();
    this.isShow = true;
  },
  methods: {
    commonError: function (result: any = null): void {
      if (result.status === 422) {
        this.validateErrors = result.data;
      }
      if (result.message !== "") {
        this.message = result.message;
      }
    },
    update: async function () {
      this.submitDisable = true;
      const result = await updateTherapistApi(
        this.$route.params.id,
        this.items
      );
      this.submitDisable = false;
      if (!result.success) {
        this.commonError(result);
        return;
      }
      alert("更新しました");
      // this.$router.push({
      //   name: "TherapistIndex",
      // });
    },
    init: function () {
      this.items.name = this.therapist.name;
      this.items.name_kana = this.therapist.name_kana;
      this.items.profile = this.therapist.profile;
    },
    destroy: async function () {
      if (!window.confirm("本当に削除しますか？")) {
        return;
      }
      this.submitDisable = true;
      const result = await destroyTherapistApi(this.$route.params.id);
      this.submitDisable = false;
      if (!result.success) {
        this.commonError(result);
        this.submitDisable = false;
        return;
      }
      alert("削除しました");
      this.$router.push({
        name: "TherapistIndex",
      });
    },
  },
});
